/** App Configuration Variables */
$primary-color: #3065ff;
$secondary-color: #204eff;
$tertiary-color: #121fa1;
$text-color-primary: #fff;
$text-color-secondary: #abebff;
$text-color-disabled: #7bbbe0;

@mixin transition-ease {
  transition: all 0.5s ease-in-out;
}

* {
  overflow-x: hidden;
}